body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 20px;
}

.form[type=submit] {
  -webkit-appearance: none;
}

.form input {
  border-radius: 4px;
  margin-left: 1em;
  margin-right: 1em;
}

.form input:focus {
  outline: 0;
}

.form input[type=text] {
  border: 1px solid #000;
  padding-left: .75rem;
  font-size: 25px;
  flex: 1 1;
}

@media only screen 
  and (max-device-width: 768px) {
    .form input[type=text] {
      font-size: 20px;
    }
  }

.form button {
	padding: 0;
	margin-right: 1.5em;
	cursor: pointer;
	border-radius: 5%;
	background-color: #22955f;
}

.form button:hover {
  background-color: #47c496;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.wasteCardDetails {
	display: grid;
	flex-wrap: nowrap;
	grid-template-columns: 5% 25% auto;
	align-items: start;
	font-size: 25px;
}

@media only screen 
  and (max-device-width: 768px) {
  	.wasteCardDetails {
  		font-size: 15px;
  		grid-template-columns: 10% 20% auto;
  	}
  }

.starButton--default, .starButton--favourite {
	border: none;
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.starButton--default .faStar {
	color: grey;
}

.starButton--favourite {
	background: transparent;
}

.faStar {
	color: #22955f;
}

button > * {
	pointer-events: none;
}

button:focus {
	outline: 0;
} 

button:hover {
	cursor: pointer;
}

.wasteBody {
	padding-left: 20%;
	padding-right: 3%;
}

.wasteBody li {
		margin-bottom: 25px;
}
.favouritesCardList {
	background-color: #f7fefa;
}

.favouritesCardList h1 {
	color: green;
}
.appHeader {
  background: linear-gradient(89deg, #1d5994 0%, #22955f 100%);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: calc(30px + 2vmin);
  font-weight: bold;
  color: white;
  text-align: center;
  margin: 0.5em 0;
}

.emptyFavourites {
	background-color: #f7fefa;
}

.emptyFavourites p {
	font-size: 20px;
}

.favouritesHeading {
	color: green;
}



